<template>
  <div class="container">

    ddddd

  </div>
</template>

<script>
    import request from '../util/request.js'

    export default {
        data() {
            return {
                list: [],
                isToggle: false,
            }
        },
        mounted() {
            this.list = this.createList(0);
        },
        methods: {
            addNewList() {
                // this.list.push(...this.createList(this.list.length))
                this.getInfoList()
            },
            createList(startNum) {
                let list = [];
                for (let i = 0; i < 15; i++) {
                    list.push({
                        src: require(`../assets/img/${i + 1}.jpg`),
                        name: `Image${startNum + i + 1}`,
                        isToggle: false,
                        title: '幸福是一种感受，关键是要善于发现幸福，抓住幸福，正如一位哲人说的：别在幸福的金马驹跑过之后，才从地上捡起几根儿金鬃毛。愿你时时都有幸福！'

                    })
                }
                return list;
            },
            handleClick(item) {
                //this.getInfoList()
            },
            getInfoList() {
                request.get("/pray").then((rsp) => {
                    if (rsp.ec == 0) {
                        this.list = this.list.concat(rsp.data)
                    }
                })
            }
        },
        components: {
        }
    }
</script>

<style lang="scss" scoped>
  * {
    margin: 0;
    padding: 0;
  }

  #app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }

  .container {
    background: #66677c;
  }

  .card {
    background: #fff;
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    transition: 0.2s;
    top: 0;

    &:hover {
      top: -3px;
    }

    .cover {
      padding: 10px 10px 0 10px;
      line-height: 0;

      img {
        width: 100%;
      }
    }

    .name {
      background: #fff;
      color: #dd4b39;
      font-weight: 600;
      padding: 10px 20px;
      font-size: 18px;
    }

    .title {
      background: #fff;
      color: #666;
      padding: 10px 20px;
      font-size: 14px;
    }
  }

  .menu-wrapper {
    padding: 20px;

    .menu {
      display: block;
      width: 180px;
      height: 40px;
      border-radius: 3px;
      background: #fff;
      border: none;
      outline: none;
      color: #666;
      margin: 0 auto;
      cursor: pointer;
    }
  }

  .flashcard {
    cursor: pointer;
    border-radius: 10px;
    margin: 10px;
    padding: 15px;
    text-align: center;
  }

  .flashcard:hover {
    box-shadow: 0 0px 25px rgba(0, 0, 0, 0.8);
  }

  .animated {
    animation-duration: 5s;
    animation-fill-mode: both;
  }

  @keyframes flipInX {
    from {
      transform: perspective(400px) rotate3d(1, 0, 0, 90deg);
      animation-timing-function: ease-in;
      opacity: 0;
    }
    40% {
      transform: perspective(400px) rotate3d(1, 0, 0, -20deg);
      animation-timing-function: ease-in;
    }
    60% {
      transform: perspective(400px) rotate3d(1, 0, 0, 10deg);
      opacity: 1;
    }
    80% {
      transform: perspective(400px) rotate3d(1, 0, 0, -5deg);
    }
    to {
      transform: perspective(400px);
    }
  }

  .flipInX {
    backface-visibility: visible !important;
    animation-name: flipInX;
  }
</style>



