<template>
  <div class="container">
    <Waterfall :list="list" :gutter="10" :width="420" :phoneCol="1" backgroundColor="rgb(73, 74, 95)"
               @handleClick="handleClick" ref="waterfall">
      <template slot="item" slot-scope="props">

        <div @click="props.data.isToggle=!(props.data.isToggle)"
             v-show="!props.data.isToggle" class="animated flipInX flashcard">

          <div class="card">
            <div class="cover"><img :src="props.data.src" alt="" @load="$refs.waterfall.refresh()"></div>

            <div class="title">
              <p>{{props.data.title}}</p>
            </div>

            <div class="name">
              <p class="iconfont cl-icon-earth"> Amen </p>
            </div>
          </div>

        </div>

        <div @click="props.data.isToggle=!(props.data.isToggle)"
             v-show="props.data.isToggle" class="animated flipInX flashcard">

          <div class="card">
            <div class="title">
              <p>{{props.data.title}} back copy </p>
            </div>

            <div class="name">
              <p>by mark haam </p>
            </div>
          </div>
        </div>

      </template>
    </Waterfall>
    <div class="menu-wrapper">
      <button class="menu" @click="addNewList">Get More</button>
    </div>
  </div>
</template>

<script>
    import Waterfall from "vue-waterfall-plugin";

    import request from '../util/request.js'

    export default {
        name: 'square',
        data() {
            return {
                list: [],
                isToggle: false,
            }
        },
        mounted() {
            this.list = this.createList(0);
        },
        methods: {
            addNewList() {
                // this.list.push(...this.createList(this.list.length))
                this.getInfoList()
            },
            createList(startNum) {
                let list = [];
                for (let i = 0; i < 18; i++) {
                    list.push({
                        src: require(`../assets/img/${i + 1}.jpg`),
                        name: `Image${startNum + i + 1}`,
                        isToggle: false,
                        title: 'Who will separate us from the love of Christ? Will hardship, or distress, or persecution, or famine, or nakedness, or peril, or sword? No, in al l these things we are more than conquerors through him who loved us.'

                    })
                }
                return list;
            },
            handleClick(item) {
                //this.getInfoList()
            },
            getInfoList() {
                request.get("/pray").then((rsp) => {
                    if (rsp.ec == 0) {
                        this.list = this.list.concat(rsp.data)
                    }
                })
            }
        },
        components: {
            Waterfall
        }
    }
</script>

<style lang="scss">
  * {
    margin: 0;
    padding: 0;
  }

  #app {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
  }

  .container {
    background: #66677c;
  }

  .card {
    background: #fff;
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;
    position: relative;
    transition: 0.2s;
    top: 0;

    &:hover {
      top: -3px;
    }

    .cover {
      padding: 10px 10px 0 10px;
      line-height: 0;

      img {
        width: 100%;
      }
    }

    .name {
      background: #fff;
      color: #dd4b39;
      font-weight: 600;
      padding: 10px 20px;
      font-size: 18px;
    }

    .title {
      background: #fff;
      color: #666;
      padding: 10px 20px;
      font-size: 14px;
    }
  }

  .menu-wrapper {
    padding: 20px;

    .menu {
      display: block;
      width: 180px;
      height: 40px;
      border-radius: 3px;
      background: #fff;
      border: none;
      outline: none;
      color: #666;
      margin: 0 auto;
      cursor: pointer;
    }
  }

  .flashcard {
    cursor: pointer;
    border-radius: 10px;
    margin: 10px;
    padding: 15px;
    text-align: center;
  }

  .flashcard:hover {
    box-shadow: 0 0px 25px rgba(0, 0, 0, 0.8);
  }

  .animated {
    animation-duration: 5s;
    animation-fill-mode: both;
  }

  @keyframes flipInX {
    from {
      transform: perspective(400px) rotate3d(1, 0, 0, 90deg);
      animation-timing-function: ease-in;
      opacity: 0;
    }
    40% {
      transform: perspective(400px) rotate3d(1, 0, 0, -20deg);
      animation-timing-function: ease-in;
    }
    60% {
      transform: perspective(400px) rotate3d(1, 0, 0, 10deg);
      opacity: 1;
    }
    80% {
      transform: perspective(400px) rotate3d(1, 0, 0, -5deg);
    }
    to {
      transform: perspective(400px);
    }
  }

  .flipInX {
    backface-visibility: visible !important;
    animation-name: flipInX;
  }
</style>



