import Vue from 'vue'
import Router from 'vue-router'
import Square from '@/components/Square'
import PrayStart from '@/components/PrayStart'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Square',
      component: Square
    },
    {
      path: '/start',
      name: 'PrayStart',
      component: PrayStart
    }
  ]
})



